import React, { useState, useEffect } from "react";
import { Container, Col, Row, Label } from "reactstrap";
import Loader from "react-loader-spinner";
import myInitObject from "../../api";
import DatatablePage from "views/DatatablePage";
import DatePicker from "react-datepicker";
import Select from "react-select";
// import "react-select/dist/react-select.css";
const moment = require("moment");
const Swap = () => {
  const [dailySwap, setdailySwap] = useState([]);
  const [loading, setloading] = useState([]);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [account, setAccount] = useState([]);
  const [symbol, setSymbol] = useState([]);
  const [act, setAct] = useState(null);
  const [sym, setSym] = useState(null);
  const [newResult, setResult] = useState([]);
  const [filterResult, setFilterResult] = useState([]);
  const [filtered, setFiltered] = useState(false);
  const [dateTime, setdateTime] = useState("");
  const [activeProfileName, setactiveProfileName] = useState("");
  const [active, setActive] = useState(false);

  useEffect(() => {
    setInterval(() => {
      if (myInitObject.responsLUT &&
        myInitObject.responsDS &&
        myInitObject.responsAC &&
        myInitObject.responsAS &&
        myInitObject.responsAF === undefined
      ) {
        console.log("no data");
      } else { 
        setdateTime(myInitObject.fetchLastUpdatedTime.filteredInfoTime);
        setloading(myInitObject.fetchdailyswaps === 0 ? true : false);
        setdailySwap(myInitObject.fetchdailyswaps.rows);
        setSymbol(myInitObject.fetchAllSymbol && myInitObject.fetchAllSymbol.rows);
        setAccount(myInitObject.fetchAllAccounts.rows);
        setResult(myInitObject.fetchdailyswaps.rows);
        setactiveProfileName(myInitObject.fetchActivefilterdata.rows[0].profile_name);
      }
    }, 1000);
  }, []);

  const dateFilter = (date, method) => {
    // console.log(date, 'call');
    let sd, ed;
    sd = startDate ? moment(startDate).format("MM-DD-yyyy") : "";
    ed = endDate ? moment(endDate).format("MM-DD-yyyy") : "";
    if (method === "start") {
      // console.log(startDate);

      sd = date && moment(date).format("MM-DD-yyyy") ;
      setStartDate(date);
    }
    if (method === "end") {
      // console.log(startDate);
      ed = date && moment(date).format("MM-DD-yyyy") ;

      setEndDate(date);
    }
    console.log(sd, ed, 'momnt');
    let result = [];
    if (filtered === true && filterResult && filterResult.length > 0) {
      result = filterResult.filter((d) => {
        let time = moment(d.date).format("MM-DD-yyyy");
        if (sd == false && ed == false) {
          return true;
        } else if (sd != false && ed == false) {
          return moment(time).isSameOrAfter(sd, "day");
        } else if (sd == false && ed != false) {
          return moment(time).isSameOrBefore(ed, "day");
        } else if (sd != false && ed != false) {
          return (
            moment(time).isSameOrAfter(sd, "day") &&
            moment(time).isSameOrBefore(ed, "day")
          );
        }
      });
      setFilterResult(result);
      setFiltered(true);
    }else if(filtered === false || active === true){
      result = newResult.filter((d) => {
        let time = moment(d.date).format("MM-DD-yyyy");
        if (sd == false && ed == false) {
          return true;
        } else if (sd != false && ed == false) {
          return moment(time).isSameOrAfter(sd, "day");
        } else if (sd == false && ed != false) {
          return moment(time).isSameOrBefore(ed, "day");
        } else if (sd != false && ed != false) {
          return (
            moment(time).isSameOrAfter(sd, "day") &&
            moment(time).isSameOrBefore(ed, "day")
          );
        }
      });
      setFilterResult(result);
      // setFiltered(true);
      setActive(true)
    }

  // let result = newResult.filter((d) => {
  //   let time = moment(d.date).format("MM-DD-yyyy");
  //   if (sd == false && ed == false) {
  //     return true;
  //   } else if (sd != false && ed == false) {
  //     return moment(time).isSameOrAfter(sd, "day");
  //     // return time >= sd;
  //   } else if (sd == false && ed != false) {
  //     return moment(time).isSameOrBefore(ed, "day");
  //     // return time <= ed;
  //   } else if (sd != false && ed != false) {
  //     return (
  //       moment(time).isSameOrAfter(sd, "day") &&
  //       moment(time).isSameOrBefore(ed, "day")
  //     );
  //   }
  // });
    // console.log(sd, ed, result);
    // setFilterResult(result);
    // setFiltered(true);
  };
  const loginData = (paramsact, ) => {
    setAct(paramsact);
    let result = []
    // console.log(sym && sym.value, 'sym && sym.value');
    if ((sym != null && sym.value)) {
      result = newResult.filter((d) => ((d.account_login === String(paramsact.value)) && (d.symbol === sym.value)) && true);
    } else {
      result = newResult.filter((d) => d.account_login === String(paramsact.value) && true);
    }
    setFilterResult(result);
    setFiltered(true);
    setStartDate('')
    setEndDate('')
    // console.log(result, 'result96');
  };
  const symbolData = (paramssym) => {
    setSym(paramssym);
    let result = []
    // console.log(act && act.value, 'act && act.value');
    if ((act != null && act.value)) {
      result = newResult.filter((d) => ((d.account_login === String(act.value)) && (d.symbol === paramssym.value)) && true);
    } else {
      result = newResult.filter((d) => d.symbol === paramssym.value && true);
    }
    setFilterResult(result);
    setFiltered(true);
    setStartDate('')
    setEndDate('')
    // console.log(result, 'result101');
  };

  const SymbolSelect = () => {
    let options = symbol && symbol.map((data) => {
      return { value: data.name, label: data.name };
    });

    return (
      <Select
        placeholder="Select Symbol..."
        value={sym}
        onChange={symbolData}
        options={options}
      />
    );
  };
  const AccountSelect = () => {
    let options = account.map((data) => {
      return { value: data.login, label: data.login };
    });

    return (
      <Select
        placeholder="Select Account..."
        value={act}
        onChange={loginData}
        options={options}
      />
    );
  };
  // console.log(filterResult,  "filterResult");
  return (
    <Container fluid>
       <Row className="no-margin">
            <div className="lastUpdate">
              <span> Last Updated : </span>
              {dateTime}(UTC)
            </div>
          </Row>
      <div className="pad-y">
        <div className="symbol-box" style={{ marginBottom: "20px" }}>
          <div className="openpositions">
            <span className="p-text">Daily Swaps</span>
          </div>
        </div>
        {loading ? (
          <Loader
            type="ThreeDots"
            color="black"
            height={100}
            width={100}
            style={{ textAlign: "center" }}
          />
        ) : (
          <div className="symbol-box">
            <Row>
              <Col md={6}>
                <label>Login</label>
                {AccountSelect()}
              </Col>
              <Col md={6}>
                <label>Symbol</label>
                {SymbolSelect()}
              </Col>
            </Row>
            <Row>
              <Col md={12} className="filter-date">
                <Label>Start Date</Label>
                <DatePicker
                  selected={startDate}
                  onChange={(date) => {
                    dateFilter(date, "start");
                  }}
                  selectsStart
                  placeholderText="mm-dd-yyyy"
                  dateFormat="MM-dd-yyyy"
                  startDate={startDate}
                  endDate={endDate}
                />
                <Label>End Date</Label>
                <DatePicker
                  selected={endDate}
                  onChange={(date) => {
                    dateFilter(date, "end");
                  }}
                  selectsEnd
                  placeholderText="mm-dd-yyyy"
                  dateFormat="MM-dd-yyyy"
                  startDate={startDate}
                  endDate={endDate}
                  minDate={startDate}
                />
              </Col>
            </Row>
            <div className="custom_dataTable">
            <DatatablePage
              data={(filtered === true||active === true) ? filterResult : newResult}
            />
            </div>
            {/* {filtered === true ? 
            <DatatablePage data={filterResult} /> : 
            <DatatablePage data={newResult} />} */}
          </div>
        )}
      </div>
    </Container>
  );
};

export default Swap;
